import Vue from 'vue';
import Vuex from 'vuex';
import * as actions from './actions';
import * as mutations from './mutations';
import * as getters from './getters';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    userInfo: {
      account: '',
      password: ''
    }, // 

    personInfo: {
      account: '',
      email: '',
      phone: '',
      username: ''
    }, // 

    categoryList: {}, // 

    cartCommodityVOList: {}, // 
    orderCommodityVOList: {}, // 

    commodityList: {}, // 
    commodityInfo: {}, // 
    commoditySize: '',

    jewelryList: {}, // 
    jewelryInfo: {}, // 

    consigneeList: {}, // 
    consigneeInfo: {}, // 

    token: '', // 
    is_login: false, // 
    signUpStep: 0, // 


    errorpopupSwitch: false, // 
    addSwitch: false, // 
    addAddressSwitch: false, // 
    cPwsSwitch: false, // 
    cInfoSwitch: false, // 
    cRASwitch: false, // 
    errorMess: ''
  },
  getters,
  actions,
  mutations
});



